import React, {useState, useEffect} from 'react';
import ContainerHeader from 'components/ContainerHeader';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import CardBox from 'components/CardBox';
// import Checkbox from '@material-ui/core/Checkbox';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import {base_url, public_url, $http} from "config";
import {useHistory, useParams} from 'react-router-dom';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import Fab from '@material-ui/core/Fab';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormLabel from '@material-ui/core/FormLabel';
import Resizer from 'react-image-file-resizer';
import SweetAlert from 'react-bootstrap-sweetalert'
import IconButton from "@material-ui/core/IconButton";
import { ReactFormGenerator } from 'react-form-builder2';
import 'react-form-builder2/dist/app.css';
import { Rotate90DegreesCcwFontIcon } from 'react-md';

const RoomInfo = (props) => {

    const [project_id, setProjectId] = useState(props.location.state ? props.location.state.project_id : null);
    const [room_id, setRoomId] = useState(props.location.state ? props.location.state.id : null);
    const [loader, setLoader] = useState(false);
    const params = useParams();
    const [project_name, setProjectName] = useState("");
    const [project_signoff, setProjectSignoff] = useState(0);
    const [room_site, setRoomSite] = useState(0);
    const [room_number, setRoomNumber] = useState("");
    const [room_height, setRoomHeight] = useState("");
    const [room_wall, setRoomWall] = useState("");
    const [room_day, setRoomDay] = useState(0);
    const [room_hour, setRoomHour] = useState(0);
    const [room_img, setRoomImg] = useState([]);
    const [room_img_len, setRoomImgLen] = useState(0);
    const [room_img_url, setRoomImgUrl] = useState([]);
    const [room_asbestos, setRoomAsbestos] = useState("2");
    const [room_notes, setRoomNotes] = useState("");
    const [room_site_rows, setRoomSiteRows] = useState([]);
    const [room_form_value,setRoomFormValue] = useState([]);
    const [room_form_id,setRoomFormId] = useState('');
    const [room_form_style,setRoomFormStyle] = useState([]);
    const [room_form_field,setRoomFormField] = useState([]);
    
    //const [room_departmentname_rows, setRoomDepartmentRows] = useState([]);
    //const [room_buildingname_rows, setRoomBuildingRows] = useState([]);
    //const [room_floorname_rows, setRoomFloorRows] = useState([]);
    //const [room_department_id, setRoomDepartmentName] = useState("");
    //const [room_building_id, setRoomBuildingName] = useState("");
    //const [room_floor_id, setRoomFloorName] = useState("");
       
    const [goback, setGoback] = useState(false)
    const [confirm_delete, setConfirmDelete] = useState(false)
    const [deleteUrl, setDeleteUrl] = useState('')

    const History = useHistory();
    const {width} = useSelector(({settings}) => settings);
    const [imgLoad, setImgLoad] = useState(false)

    useEffect(() => {
        if (imgLoad) return
        setImgLoad(true)
        $http.get(base_url + "room/roomInfo", {params: {id: params.room_id, project_id: params.project_id}})
        .then((response) => {
            if (response.data) {
                setRoomId(response.data.room_id)
                setProjectId(response.data.project_id)
                setRoomSiteRows(response.data.sites)
                setProjectSignoff(response.data.project_signoff)
                //setRoomDepartmentRows(response.data.departments)
                //setRoomBuildingRows(response.data.buildings)
                //setRoomFloorRows(response.data.floors)
                var img_url = []
                if (response.data.room_id) {
                    setRoomSite(response.data.room.site_id);
                    //setRoomDepartmentName(response.data.room.department_id);
                    //setRoomBuildingName(response.data.room.building_id);
                    //setRoomFloorName(response.data.room.floor_id);
                    setRoomNumber(response.data.room.room_number);
                    setRoomDay(response.data.room.estimate_day);
                    setRoomHour(response.data.room.estimate_time);
                    setRoomHeight(response.data.room.ceiling_height);
                    setRoomWall(response.data.room.wall);
                    setRoomAsbestos(response.data.room.asbestos);
                    setRoomNotes(response.data.room.notes);
                    setProjectName(response.data.room.project_name);
                    setRoomFormValue(response.data.room.form_value);
                    setRoomFormId(response.data.room.location_form_id);
                    setRoomFormStyle(response.data.room.form_style);
                    setRoomFormField(response.data.form_fields)

                    if(response.data.room.img_files.length > 0){
                        setRoomImgLen(response.data.room.img_files.length);
                        (response.data.room.img_files).forEach(url =>{
                            img_url.push({id:url.id,url:public_url + 'upload/img/' + url.img_name});
                        })
                    }
                    if (localStorage.getItem('pixie_room_id')) {
                        let pixie_room_id = localStorage.getItem('pixie_room_id')
                        let pixie_id = localStorage.getItem('pixie_id')
                        let pixie_data = localStorage.getItem('pixie_data')
    
                        if (response.data.room_id == pixie_room_id) {
                            let img_item = response.data.room.img_files.find((item) => {return item.id == pixie_id || item.img_name == pixie_id})
                            let arr = pixie_data.split(','),
                            mime = arr[0].match(/:(.*?);/)[1],
                            bstr = atob(arr[1]), 
                            n = bstr.length, 
                            u8arr = new Uint8Array(n);

                            while (n--) {
                                u8arr[n] = bstr.charCodeAt(n)
                            }
                            let file_name = JSON.parse(localStorage.getItem('user')).id + '-' + new Date().getTime() + '-' + 'pixie.' + mime.split('/').pop()
                            let pixie_obj = new File([u8arr], file_name, {type: mime})
                            if (img_item) {
                                img_url = img_url.filter((item) => {return item.id != img_item.id})
                            }

                            img_url.push({id: file_name, url: URL.createObjectURL(pixie_obj)})
                            room_img.push(pixie_obj)
                            setRoomImgUrl(img_url)
                            setRoomImg(room_img)
                        }
    
                        localStorage.removeItem('pixie_room_id')
                        localStorage.removeItem('pixie_id')
                        localStorage.removeItem('pixie_data')
    
                    } else {
                        setRoomImgUrl(img_url);
                    }
                }
            }
            setLoader(true);
        })
        .catch((error) => {
            setGoback(true)
        })

        $http.get(base_url + "room/editPhoto")
        .then((response) => {
            if (response.data.status === "success") {

            }
        })
        .catch((error) => {
            setGoback(true)
        })
    }, []);

    useEffect(() => {
        if (loader || room_img.length > 0) {
            if (room_img.length == 0 && room_img_url.filter((item) => {return item.url.indexOf('blob:http') == 0}).length > 0)
                return
            SaveRoom(1)
        }
    }, [room_img, room_img_url])
    
    useEffect(()=>{
        if(loader){
            room_form_value.map(item=>{
                if(item.field_type =="Checkboxes"){
                    if (document.getElementById(`fid_preview_${item.field_name}`) != null) {
                        document.getElementById(`fid_preview_${item.field_name}`).checked = item.is_checked==='1'?true:false
                    }
                } else if (item.field_type =="RadioButtons") {
                    if (document.getElementById(`fid_preview_${item.field_name}`) != null) {
                        document.getElementById(`fid_preview_${item.field_name}`).checked = item.is_checked==='1'?true:false
                    }
                } else if (item.field_type == "Rating") {
                    let width = (item.field_value / 5) * 100 +'%'
                    if (document.getElementsByClassName('rating-stars').length > 0) {
                        document.getElementsByClassName('rating-stars')[0].style.width = `${width}`
                    }
                    if (document.getElementsByName(`${item.field_name}`).length > 0) {
                        document.getElementsByName(`${item.field_name}`)[0].value = item.field_value
                    }
                } else if (item.field_type =="Signature") {
                    var canvas = document.querySelector(".react-form-builder-form canvas");
                    if (canvas != null) {
                        var context = canvas.getContext('2d');
                        var img = new Image();
                        img.onload = function() {
                            context.drawImage(this, 0, 0, canvas.width, canvas.height);
                        }
                        img.src = item.field_value
                    }
                } else {
                    if (document.getElementsByName(`${item.field_name}`).length > 0) {
                        document.getElementsByName(`${item.field_name}`)[0].value = item.field_value
                    }
                }
            })
            
        }

    },[loader])


    const removeImage=(data)=>{
        setRoomImgUrl(room_img_url.filter((item) => {return item.id != data.id}))
        setRoomImg(room_img.filter((item) => {return item.name != data.id}))
    }

    const resizeFile = (file) => new Promise(resolve => {
        Resizer.imageFileResizer(file, 500, 500, 'JPEG', 100, 0,
        uri => {
          resolve(uri);
        },
        'blob'
        );
    });

    
    const uploadMultipleFiles = async (e) => {
        var fileObj = [];
        var img_url = [...room_img_url]
        var send_files = []
        var files = e.target.files
        for(let i = 0; i < files.length; i++) {
            let img_file_name = JSON.parse(localStorage.getItem('user')).id + '-' + new Date().getTime() + '-' + files[i].name
            const image = await resizeFile(files[i]);
            send_files.push(new File([image],img_file_name,image))
        }

        for (let i = 0; i < send_files.length; i++) {
            img_url.push({id:send_files[i].name,url:URL.createObjectURL(send_files[i])});
        }

        fileObj = [...room_img];
        for(let i = 0; i < send_files.length; i++) {
            fileObj.push(send_files[i]);
        }
        setRoomImgUrl(img_url);
        setRoomImg(fileObj);
    }
    const SaveRoom = (save_type) => {
        let values = []
        let value = ''
        let field_names = room_form_field.filter(item=>item.new_form_id==room_form_id)
        values = field_names.map(item=>{
            let is_checked =''
            if (item.field_type =="RadioButtons"||item.field_type =="Checkboxes"){
                is_checked = document.getElementById(`fid_preview_${item.field_name}`).checked
                value = document.getElementById(`fid_preview_${item.field_name}`).value
            }
            else if (item.field_type =="Signature"){
                value = document.querySelector(".react-form-builder-form canvas").toDataURL()
            }
            else
                value = document.getElementsByName(`${item.field_name}`)[0].value
            return {
                field_name:item.field_name,
                field_label:item.field_label,
                field_type:item.field_type,
                field_value:value,
                form_type:item.form_type,
                is_checked:is_checked,
                new_form_id:room_form_id
            }
        })

        if (!navigator.onLine) {
            caches.open('SirvezApp').then(function(cache) {

                let file_array = [], fileName, pos

                room_img_url.forEach((item) => {
                    if (item.id != '0' && item.url.indexOf('blob:http') < 0) {
                        pos = item.url.indexOf('upload/img/')
                        fileName = item.url.substr(pos + 11)
                        file_array.push({id: item.id, img_name: fileName})
                    }
                })
        
                room_img.forEach((item) => {
                    cache.put('/upload/img/' + item.name, new Response(item))
                    file_array.push({id: item.name, img_name: item.name})
                })

                // save into room info
                //let request_room = params.room_number.replace(/ /g, '+')
                //let request_project = params.project_name.replace(/ /g, '+')
                let request_info = '/api/room/roomInfo?room_id=' + params.room_id + '&project_id=' + params.project_id
                cache.match(request_info)
                .then(function (response) {return response ? response.json() : null})
                .then(function (data) {
                    if (data == null) return
                    data.room.img_files = file_array
                    data.room.site_id = room_site
                    data.room.room_number = room_number
                    data.room.estimate_day = room_day
                    data.room.estimate_time = room_hour
                    data.room.ceiling_height = room_height
                    data.room.wall = room_wall
                    data.room.asbestos = room_asbestos

                    const jsonResponse = new Response(JSON.stringify(data), {
                        headers: {'content-type': 'application/json'}
                    })
                    cache.put(request_info, jsonResponse.clone())
                })

                // save into room list
                let request_list = '/api/project/projectInfo?project_id=' + params.project_id
                cache.match(request_list)
                .then(function (response) {return response ? response.json() : null})
                .then(function (data) {
                    if (data == null) return
                    let item = data.rooms.find((item) => {return item.id == room_id})
                    if (item) {
                        item.site_id = room_site
                        item.room_number = room_number
                        item.estimate_day = room_day
                        item.estimate_time = room_hour
                        item.ceiling_height = room_height
                        item.wall = room_wall
                        item.asbestos = room_asbestos

                        const jsonResponse = new Response(JSON.stringify(data), {
                            headers: {'content-type': 'application/json'}
                        })
                        cache.put(request_list, jsonResponse.clone())
                    }
                })
            })
        }

        var temp = '';
        var signed_off = (!room_id&&project_signoff==1)?2:0
        const frmData = new FormData();
        frmData.append('id', room_id);
        frmData.append('site_id', room_site);
        //frmData.append('department_id', room_department_id);
        //frmData.append('building_id', room_building_id);
        //frmData.append('floor_id', room_floor_id);
        frmData.append('room_number', room_number);
        frmData.append('estimate_day', room_day);
        frmData.append('estimate_time', room_hour);
        frmData.append('ceiling_height', room_height);
        frmData.append('signed_off', signed_off);
        frmData.append('wall', room_wall);
        for (var i = 0; i < room_img.length; i++)
            frmData.append('room_img['+i+']', room_img[i]);
        for (var i = 0; i < room_img_url.length; i++)
            temp+=room_img_url[i].url;
        frmData.append('img_array', temp);
        frmData.append('asbestos', room_asbestos);
        frmData.append('notes', room_notes);
        frmData.append('field_values',JSON.stringify(values))
       
        if (project_id) {
            frmData.append('project_id', project_id);
            //frmData.append('customer_id', Project.company_id);
        }
        $http.post(base_url + "room/updateRoom", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                // NotificationManager.success("Update Room", "Success!", 1000, () => {
                // });
                if(save_type!=1)
                    History.push({
                        pathname: `/app/project/live/${project_id}/${response.data.room.id}`
                    })
              
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                });
            }
        }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
        })
    }

    const ModifyRoom = (data,index) => {
        var sendfile = null;
        if (data.url.indexOf('blob:http') == 0) {
            sendfile = room_img.find((item) => {return item.name == data.id})
        }
        localStorage.setItem('isFirst', 0);
        History.push({
            pathname: `/app/project/sticker`,
            state: {
                room_id: room_id,
                img_id: data.id,
                img_url: data.url,
                img_file: sendfile                
            },
        })
    }

    return (
        <div className="app-wrapper">
            <ContainerHeader 
            match={props.match} 
            project_name = {project_name}
            room_number = {room_number}
            title="Room"
            />
            { loader ? (
            <div className="row">
                <CardBox styleName="col-lg-12">
                    <form className="row" autoComplete="off">
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_project"
                                label="Project"
                                // value={loader ? Project.project_name : ''}
                                value={project_name}
                                margin="normal"
                                helperText="Please Enter Project"
                                fullWidth
                                disabled
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_site"
                                select
                                label="Site"
                                value={room_site}
                                onChange={(event)=>setRoomSite(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Site"
                                margin="normal"
                                fullWidth
                            >
                                {room_site_rows.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.site_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        {/* <div className="col-md-6 col-12">
                            <TextField
                                id="room_department"
                                select
                                label="department"
                                value={room_department_id}
                                onChange={(event)=>setRoomDepartmentName(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Department"
                                margin="normal"
                                fullWidth
                            >
                                {room_departmentname_rows.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.department_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div> */}
                        {/* <div className="col-md-6 col-12">
                            <TextField
                                id="room_building"
                                select
                                label="building"
                                value={room_building_id}
                                onChange={(event)=>setRoomBuildingName(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Building"
                                margin="normal"
                                fullWidth
                            >
                                {room_buildingname_rows.filter(option => {
                                        return option.site_id == room_site
                                    }).map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.building_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div> */}
                        {/* <div className="col-md-6 col-12">
                            <TextField
                                id="room_floor"
                                select
                                label="floor"
                                value={room_floor_id}
                                onChange={(event)=>setRoomFloorName(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Floor"
                                margin="normal"
                                fullWidth
                            >
                                {room_floorname_rows.filter(option => {
                                        return option.building_id == room_building_id
                                    }).map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.floor_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div> */}
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="Location"
                                label="Location"
                                value={room_number}
                                onChange={(event)=>setRoomNumber(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Location"
                                fullWidth
                                />
                        </div>
                        {room_form_id ==='0'?
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_height"
                                label="Ceiling Heights"
                                value={room_height}
                                onChange={(event)=>setRoomHeight(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Ceiling Heights"
                                fullWidth
                                />
                        </div>
                        :null}
                        {room_form_id ==='0'?
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="room_wall"
                                label="Wall"
                                value={room_wall}
                                onChange={(event)=>setRoomWall(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Wall"
                                fullWidth
                                />
                        </div>
                        :null}
                        {room_form_id ==='0'?
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_day"
                                select
                                label="Installation Estimate Days"
                                value={room_day}
                                onChange={(event)=>setRoomDay(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Days"
                                margin="normal"
                                fullWidth
                            >
                                {[...Array(24)].map((id, value) => (
                                    <MenuItem key={value} value={value}>
                                        {value}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        :null}
                        {room_form_id ==='0'?
                        <div className="col-md-6 col-12">
                            <TextField
                                id="room_hour"
                                select
                                label="Installation Estimate Hours"
                                value={room_hour}
                                onChange={(event)=>setRoomHour(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Days"
                                margin="normal"
                                fullWidth
                            >
                                {[...Array(10)].map((id, value) => (
                                    <MenuItem key={value} value={value}>
                                        {value}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        :null}
                        {room_form_id ==='0'?
                        <div className="col-md-6 col-12 d-flex align-items-center">
                            
                            <FormControl component="fieldset" required>
                                <FormLabel component="legend">Asbestos</FormLabel>
                                <RadioGroup
                                className="d-flex flex-row"
                                aria-label="gender"
                                name="gender"
                                value={room_asbestos}
                                onChange={(event, value) => setRoomAsbestos(value)}
                                >
                                <FormControlLabel value="1" control={<Radio color="primary"/>} label="Yes"/>
                                <FormControlLabel value="0" control={<Radio color="primary"/>} label="No"/>
                                <FormControlLabel value="2" control={<Radio color="primary"/>} label="Unknown"/>
                                </RadioGroup>
                            </FormControl>
                        </div>
                        :null}
                        {room_form_id ==='0'?
                        <div className="col-md-12 col-12">
                            <TextField
                                id="room_notes"
                                label="Notes"
                                multiline
                                variant="outlined"
                                rowsMax="4"
                                value={room_notes}
                                onChange={(event) => setRoomNotes(event.target.value)}
                                margin="normal"
                                fullWidth
                            />
                        </div>
                        :null}
                        {room_form_id?
                            <div className="col-md-12 col-12">
                                <ReactFormGenerator
                                    download_path=""
                                    back_action="/"
                                    back_name="Back"
                                    action_name="Save"
                                    form_action="/"
                                    form_method="POST"
                                    hide_actions={true}
                                    data={room_form_style} 
                                />
                            </div>
                        :null}
                        <div className="bg-primary text-center w-100 mt-4 mb-4">
                            <input type="file" id="room_img" accept="image/*" multiple 
                                name="room_img[]" style={{display: "none"}}
                                onChange={(event) => {
                                    uploadMultipleFiles(event);
                                    //setRoomImg(event.target.files[0]);
                                }}/>
                            <IconButton className="text-grey"
                                onClick={(e)=>{document.getElementById('room_img').click()}}>
                                <i className="zmdi zmdi-camera zmdi-hc-2x animated infinite pulse"/>
                            </IconButton>
                        </div>
                        {/* <div className="col-md-12 col-12 my-4 text-center">
                            <input type="file" id="room_img" accept="image/*" multiple 
                                name="room_img[]" style={{display: "none"}}
                                onChange={(event) => {
                                    uploadMultipleFiles(event);
                                    //setRoomImg(event.target.files[0]);
                                }}/>
                            <Button className="jr-btn" color="primary"
                                variant="contained" onClick={(e)=>{document.getElementById('room_img').click()}}>
                                <AttachFile/> Add Image
                            </Button>
                        </div> */}
                            {room_img_url.map( (url,index) => 
                                <div className="col-xl-3 col-lg-4 col-md-4 col-12" key={index}>
                                <div className="jr-card pb-2">
                                    <div className="jr-card-thumb">
                                        <img className="card-img-top img-fluid" alt="products" src={url.url}/>
                                    </div>
                                    
                                    <div className="jr-card-social text-center">
                                        <div className = "row">
                                            <div className="col-xl-6 col-lg-6 col-md-6 col-6">
                                                <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" 
                                                    onClick = {() => ModifyRoom(url,index)}>
                                                    <i className="zmdi zmdi-puzzle-piece zmdi-hc-2x"/>
                                                </Fab>
                                            </div>
                                            <div className="col-xl-6 col-lg-6 col-md-6 col-6">
                                                <Fab className="jr-fab-btn bg-red accent-2 text-white"  
                                                    onClick = {()=>{setDeleteUrl(url) 
                                                            setConfirmDelete(true)}}>
                                                    <i className="zmdi zmdi-delete zmdi-hc-2x"/>
                                                </Fab>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                </div>
                            )}
                        <div className="col-md-12 col-12 text-right">
                            <Button variant="contained" color="primary" className="jr-btn" 
                                onClick={() => SaveRoom()}>
                                Save
                            </Button>
                        </div>
                    </form>
                </CardBox>
            </div>
            ):(
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>
            )}
            <SweetAlert show={goback}
                warning
                confirmBtnText="Go Back"
                confirmBtnBsStyle="danger"
                cancelBtnBsStyle="default"
                title="Warning!"
                onConfirm={() => History.go(-1)}
            >                
            Data not cached, so you can not view this page.
            </SweetAlert>

            <SweetAlert show={confirm_delete}
                warning
                showCancel
                confirmBtnText="Confirm"
                confirmBtnBsStyle="danger"
                cancelBtnBsStyle="default"
                title="Warning!"
                onConfirm={() => {setConfirmDelete(false) 
                    removeImage(deleteUrl)}}
                onCancel = {()=>setConfirmDelete(false)}
            >                
            Are you sure you want to delete?
            </SweetAlert>

            <NotificationContainer/>
        </div>
    );
}

export default RoomInfo;